import amplop2 from "./amplop2.png";
import kado1 from "./kado1.png";
import bca from "./bca.png";
import masker from "./masker.png";
import jarak from "./jarak.png";
import vaksin from "./vaksin.png";
import tangan from "./tangan.png";
import React from "react";
import {CopyToClipboard} from 'react-copy-to-clipboard';
import swal from "sweetalert";
function Kado() {
    return (
      <div className="kado" id="amplop">
        <div className="amplop">
      <img src={amplop2} alt="amplop" width="65px" height="50px"/>
      <h2>Amplop Online</h2>
      <p>Do'a restu anda paling berarti bagi kami. Tanda kasih akan kami terima dengan baik :)</p>
      <div class="mediaAmplop">
                <img className="imgRek" src={bca} width="100px" height="50px" alt="rekening bca"/>
                <p>
                  NAMA REKENING BCA:
                  <br/>
                  <div className="divcopi">ELIYAH NOVIYANI</div>
                  <br/>
                </p>
                <p>
                  NOMOR REKENING BCA:
                  <br/>
                  <div className="divcopi">3040890725</div>
                  <br/>
                  <CopyToClipboard text='3040890725'>
                    <button onClick={myFunction} className="button">salin nomor</button>
                  </CopyToClipboard>
                </p>   
      </div>
  </div>
  <div className="kadoDua">
      <h2>Kirim Kado</h2>
          <div className="imgkadodua">
            <img src={kado1} alt="kado" width="65px" height="75px"/>
          </div>
          <br/>
          <div className="divcopi">BLOK WAGE RT/RW 02/02, DESA HULUBANTENG LOR,<br/> KECAMATAN PABUARAN, KABUPATEN CIREBON JAWA BARAT</div>
                <br/>
                <CopyToClipboard text='BLOK WAGE RT/RW 02/02, DESA HULUBANTENG LOR, KECAMATAN PABUARAN, KABUPATEN CIREBON JAWA BARAT'>
                <button onClick={myFunction} className="button">salin alamat</button>
                </CopyToClipboard>
  </div>
      <h3>kesehatan bukanlah pilihan, tetapi sesuatu yang harus diperjuangkan!. seperti cintamu padanya :) hehehe..</h3>
  <div class="protokol">
      <h2>protokol kesehatan</h2>
      <div className="listProt">
        <img src={masker} alt="masker" width="80px" height="80px" />
        <img src={tangan} alt="tangan" width="80px" height="80px" />
        <br/>
        <img src={vaksin} alt="vaksin" width="90px" height="90px" />
        <img src={jarak} alt="jarak" width="140px" height="100px" />
      </div>
  </div>
      </div>
    );

  }
  function myFunction(){
    swal({
      title: "Terimakasih :)",
      text: "Teks sudah disalin!",
      icon: "success",
      button: "Ok",
      });
  }
  
  export default Kado;