import React from "react";
import {CopyToClipboard} from 'react-copy-to-clipboard';
import swal from "sweetalert";
function Akad() {
    return (
      <div className="akad" id="akad">
                <h2>Akad Nikah</h2>
                <h3>
                  Kamis 13 Oktober 2022
                  <br/>
                  ~ Di Rumah mempelai Wanita ~
                </h3>
                <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d247.5663033408171!2d108.72513761789519!3d-6.883314065613155!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0xe292638151669488!2zNsKwNTInNTkuNyJTIDEwOMKwNDMnMzAuNiJF!5e0!3m2!1sid!2sid!4v1662954269351!5m2!1sid!2sid" style={iframestyle} title="alamat" ></iframe>
                <p>
                    Pada Alamat:
                    <br/>
                    BLOK WAGE RT/RW 02/02, DESA HULUBANTENG LOR, 
                    <br/>
                    KECAMATAN PABUARAN, KABUPATEN CIREBON JAWA BARAT
                </p>
                <CopyToClipboard text='BLOK WAGE RT/RW 02/02, DESA HULUBANTENG LOR, KECAMATAN PABUARAN, KABUPATEN CIREBON JAWA BARAT'>
                <button onClick={myFunction} className="button">salin alamat</button>
                </CopyToClipboard>
                <CopyToClipboard text='https://goo.gl/maps/CuJkVtuDqrS8bSTN9'>
                <button onClick={myFunction} className="button">salin link alamat</button>
                </CopyToClipboard>
                <p>
                    "Jika ada manusia belum hidup bersama pasangannya, berarti hidupnya akan timpang dan tidak berjalan sesuai dengan ketetapan Allah SWT dan orang yang menikah berarti melengkapi agamanya, sabda Rasulullah SAW: "Barangsiapa diberi Allah seorang istri yang sholihah, sesungguhnya telah ditolong separoh agamanya. Dan hendaklah bertaqwa kepada Allah separoh lainnya." (HR. Baihaqi).
                </p>
                <h1>~~~</h1>
       </div>
    ); 
  }
  
  
  const iframestyle ={
    width : "300px",
    height:"250px",
    border:"2px solid #696a4c",
    borderRadius:"5px",
    allowfullscreen:"",
    loading:"lazy",
    referrerpolicy:"no-referrer-when-downgrade"
  }
  function myFunction(){
    swal({
      title: "Terimakasih :)",
      text: "Teks sudah disalin!",
      icon: "success",
      button: "Ok",
      });
  }
  
  
  
  
  export default Akad;